body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.list-container {
  border: 1px solid #999;
  border-radius: 8px;
  margin: 0 8px 8px 0;
  padding: 8px; }
  .list-container .list-header {
    font-weight: bold; }
  .list-container label {
    font-size: 0; }

.editable-text-field input {
  padding: 6px 12px; }

.editable-text-field span {
  display: inline-block;
  padding: 6px 0; }

.editable-text-field .placeholder {
  color: #aaa; }

.editable-text-field .btn-primary {
  margin-left: 8px; }

.item-header div.editable-text-field {
  margin-left: 1.5em; }

.item-container {
  border: 1px solid #999;
  border-radius: 8px;
  margin: 0 8px 8px 0;
  padding: 8px; }
  .item-container .item-header {
    font-weight: bold; }
    .item-container .item-header .editable-text-field {
      margin-left: 1.5em; }
  .item-container label {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    padding: 0;
    position: absolute;
    width: 1px; }
  .item-container .order {
    float: left;
    margin-right: 8px;
    padding: 6px 0; }

.list-name label {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  width: 1px; }

.list-name span {
  font-size: 150%;
  font-weight: bold; }

.list-description label {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  width: 1px; }

.valid-feedback {
  display: block;
  color: #28a745;
  font-size: 80%;
  margin-top: .25rem;
  width: 100%; }
  .valid-feedback::before {
    content: "\2714   "; }

.invalid-feedback {
  color: #dc3545;
  font-size: 80%;
  margin-top: .25rem;
  width: 100%; }
  .invalid-feedback::before {
    content: "\2718   "; }

.flash-error {
  border-radius: 8px;
  padding: 8px; }
  .flash-error.error {
    border: 1px solid #dc3545;
    color: #dc3545; }
    .flash-error.error::before {
      content: "\2718   "; }
  .flash-error.success {
    border: 1px solid #28a745;
    color: #28a745; }
    .flash-error.success::before {
      content: "\2714   "; }

